<template>
  <div class="detail">
    <Spin fix v-if="loading"></Spin>
    <div style="width:600px" v-if="taskInfo">
      <p class="f20 fw mb-20"><a class="mr-20" @click="back">返回</a> 任务详情</p>
      <p>任务编号：{{taskInfo.mch_id}}</p>
      <p>提交时间：{{dateFromat(taskInfo.create_time)}}</p>
      <div class="flex">
        <p>短信内容:</p>
        <div class="msg-content">
          {{taskInfo.context}}
        </div>
      </div>
      <p>提交数：{{data.length}}</p>
      <div class="flex-spb">
        <p>发送数：{{sendNum}}</p>
        <p>成功数：{{taskInfo.successful}}</p>
        <p>失败数：{{taskInfo.failure}}</p>
      </div>
      <p>接受号码：</p>
    </div>
    <Table :columns="columns" :data="contact_list" border></Table>
    <div class="pageBox">
      <Page :total="data.length" :pageSize="pageSize" @on-change="pageChange"></Page>
    </div>
  </div>
</template>
<script>
import { dateFormate } from '@/utils/dateFormate'
export default {
  props: ['id'],
  data () {
    return {
      loading: false,
      columns: [
        {
          title: '编号',
          render: (h, params) => {
            return h('span', {}, (this.page - 1) * this.pageSize + params.index + 1)
          }
        },
        {
          title: '手机号码',
          key: 'phone_num'
        },
        {
          title: '发送时间',
          render: (h, params) => {
            return h('span', {}, params.row.sendTime ? dateFormate(new Date(params.row.sendTime * 1000), 'yyyy-MM-dd hh:mm:ss') : '')
          },
        },
        {
          title: '发送状态',
          render: (h, params) => {
            let str = ''
            switch (params.row.state) {
              case 0:
                str = '未发送'
                break;
              case 1:
                str = '成功'
                break;
              case 2:
                str = '失败'
                break;
              default:
                break;
            }
            return h('span', {}, str)
          },
        },
      ],
      data: [],
      taskInfo: null,
      page: 1,
      pageSize: 10,
    }
  },
  mounted () {
    this.getDetail()
  },
  computed: {
    contact_list () {
      return this.data.slice((this.page - 1) * this.pageSize, this.page * this.pageSize)
    },
    sendNum () {
      return parseInt(this.taskInfo.successful + this.taskInfo.failure)
    },
    // succ_rate () {
    //   if (this.sendNum) {
    //     return (Math.round(this.taskInfo.successful / this.sendNum * 10000) / 100.00 + '%')
    //   } else {
    //     return '0.00%'
    //   }

    // },
    // finish_rate () {
    //   return (Math.round(this.taskInfo.successful / this.data.length * 10000) / 100.00 + '%')
    // }
  },
  methods: {
    GetPercent (num, total) {
      num = parseFloat(num);
      total = parseFloat(total);
      if (isNaN(num) || isNaN(total)) {
        return "-";
      }
      return total <= 0 ? "0%" : (Math.round(num / total * 100) / 100.00);
    },
    getDetail () {
      this.loading = true
      this.$post('cgi/get_mission', { id: this.id }).then(resData => {
        this.loading = false
        if (resData.ret_code == 0) {
          this.taskInfo = resData.ret_data
          this.data = resData.ret_data.contact_list
        }
      }).catch(() => {
        this.loading = false
      })
    },
    back () {
      this.$emit('back')
    },
    dateFromat (time) {
      return dateFormate(new Date(time * 1000), 'yyyy-MM-dd hh:mm:ss')
    },
    pageChange (page) {
      this.page = page
    }
  }
}
</script>
<style lang="less" scoped>
.detail {
  .msg-content {
    margin-left: 10px;
    width: 500px;
    border: 1px solid #eeeeee;
    line-height: 18px;
    border-radius: 4px;
    min-height: 120px;
    padding: 10px;
    word-break: break-all;
  }
  p {
    line-height: 30px;
    margin: 10px 0;
  }
}
</style>